$bfsize:10px;

@media(min-width: 768px) {
    .timeline {
        margin-top: 2*$bfsize;

        & > li {
            min-height: 10*$bfsize;
            display: block;
            box-sizing: border-box;
            font-size: 1.6*$bfsize;
            display: flex; /*Flex布局*/
            align-items: center; /*指定垂直居中*/
            & > div {
                display: flex; /*Flex布局*/
                justify-content: center;
                flex-direction: column;
            }

            & > .date {
                text-align: right;

                &::after {
                    position: absolute;
                    top: 50%;
                    right: -7px;
                    z-index: 10;
                    display: block;
                    width: 12px;
                    height: 12px;
                    box-sizing: border-box;
                    margin-top: -6px;
                    content: " ";
                    background-color: #eee;
                    border: 2px solid #fff;
                    border-radius: 6px;
                }
            }

            & > .main {
                min-height: 10*$bfsize;

                &:not(:last-child) {
                    border-bottom: 1px dashed #ccc;
                }

                blockquote {
                    border-left: 4px solid #dfdfdf;
                    padding-left: 10px;
                }
            }
        }
    }
}

@media(max-width: 769px) {
    .timeline {
        margin-top: 0;

        & > li {
            & > .date {
                padding-left: 0;
                font-weight: bold;
                margin-top: 2.8*$bfsize;
                margin-bottom: 0.4*$bfsize;
                font-size: 1.6*$bfsize;
            }
        }
    }
}

.timeline {
    list-style-type: none;
    box-sizing: border-box;
    padding-left: 0;
    width:100%;
}

div.main {
    padding: 2*$bfsize;
    display: inline-block;
    border-left: #eee solid 2px;

    p {
        margin-top: 10px;
        box-sizing: border-box;
    }
}

